/* @import-normalize; */

body {
  font-family: sans-serif;
  line-height: 1.5;
  margin: 0;
  color: white;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
}
ul {
  text-decoration: none;
}
li {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  margin: auto;
  width: 100px;
}
